/* ========== Loader ========== */

.loaders {
	width: 100%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex: 0 1 auto;
	    flex: 0 1 auto;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}
.loaders .loader {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex: 0 1 auto;
	    flex: 0 1 auto;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-positive: 1;
	    flex-grow: 1;
	-ms-flex-negative: 0;
	    flex-shrink: 0;
	-ms-flex-preferred-size: 25%;
	    flex-basis: 25%;
	max-width: 25%;
	height: 200px;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}
.loader {
	display: table;
	height: 100%;
	position: fixed;
	width: 100%;
	z-index: 1200;
}
.loader-bg {
	background: #35CBDF;
}
.loader-inner {
	display: table-cell;
	text-align: center;
	vertical-align: middle;
}

.loader .ball-clip-rotate-pulse {
    left: 50%;
    position: absolute;
    top: 50%;
}

/* ========== Top Navigation ========== */
.top-nav {
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    height: 80px;
    -o-transition: 0.5s ease-in;
    transition: 0.5s ease-in;
